import MovieItem from '../../components/MovieItem.vue'
import movie from '../../test.json'
export default {
	data() {
		return {
			movie: {},
			isFold: true,
			isLoading: true
		};
	},
	onLoad(options) {
		
			uni.showLoading({
				title:'加载中'
			})
	
		this.$_request({
			url:`/subject/${options.id}`
		}).then(res=>{
			console.log(res)
			this.movie = res
			this.isLoading = false
		
				uni.hideLoading()
			
		})

	},
	mounted() {

	},
	methods: {
		toggleFold() {
			this.isFold = !this.isFold
		}
	},
	components: {
		MovieItem
	}
}
